import * as React from 'react'

// DND utils
import { SortableContainer } from 'react-sortable-hoc'
import arrayMove from 'array-move'

// Feature-specific components
import { List } from '../../components/default/topSites'
import createWidget from '../../components/default/widget'

// Component group
import TopSiteTile from './topSiteTile'
import AddTopSiteButton from './addTopSiteButton'

interface Props {
  gridSites: any
  actions: any
}

const DynamicList = SortableContainer((props: any) => <List {...props} />)

class TopSitesList extends React.PureComponent<Props, {}> {
  onDragEnd = ({ oldIndex, newIndex }: any) => {
    // do not update topsites order if the drag destination is a pinned tile
    if (this.props.gridSites[newIndex].pinned) {
      return
    }
    const items = arrayMove(this.props.gridSites, oldIndex, newIndex)
    this.props.actions.topSitesDataUpdated(items)
  }

  render () {
    const { actions, gridSites } = this.props
    return (
      <>
        <DynamicList
          blockNumber={gridSites.length}
          onSortEnd={this.onDragEnd}
          axis='xy'
          lockToContainerEdges={true}
          lockOffset={'15%'}
        >
          {gridSites.map((value: any, index: any) => (
            <TopSiteTile
              key={value.id}
              actions={actions}
              index={index}
              value={value}
              // do not allow sorting pinned items
              disabled={value.pinned != null}
            />
          ))}
          <AddTopSiteButton actions={actions} topSitesLength={gridSites.length} />
        </DynamicList>
      </>
    )
  }
}

export default createWidget(TopSitesList)
