// Types

import { NewTab } from './__exclude_from_git_common/new_tab_definitions'
// Helpers
import { debounce } from './__exclude_from_git_common/debounce'

// Key to be used by localStorage
const keyName = 'NewTab-data'

export const defaultTopSitesState: any = {
  gridSites: [],
  topSites: [],
  ignoredTopSites: [],
  pinnedTopSites: [],
  bookmarks: {}
}

export const defaultState: NewTab.State = {
  ...defaultTopSitesState,
  stats: {
    adsBlockedStat: 0,
    javascriptBlockedStat: 0,
    httpsUpgradesStat: 0,
    fingerprintingBlockedStat: 0
  }
}

export const load = (): NewTab.State => {
  const data = window.localStorage.getItem(keyName)
  let state = defaultState
  if (data) {
    try {
      state = JSON.parse(data)
    } catch (error) {
      console.error('Could not parse local storage data: ', error)
    }
  }
  return state
}

export const debouncedSave = debounce<NewTab.State>((data: NewTab.State) => {
  if (data) {
    window.localStorage.setItem(keyName, JSON.stringify(data))
  }
}, 50)
