import * as React from 'react'

// Feature-specific components
import { TileButton } from '../../components/default'
import NewTopSiteInfoModal from './newTopSiteInfoModal'

interface Props {
  actions: any
  topSitesLength: any
}

interface State {
  shouldCloseDialog: boolean
}

export default class AddTopSiteButton extends React.PureComponent<Props, State> {
  constructor (props: Props) {
    super(props)
    this.state = { shouldCloseDialog: true }
  }

  shouldCloseDialog = () => {
    this.setState({ shouldCloseDialog: !this.state.shouldCloseDialog })
  }

  render () {
    const { actions, topSitesLength } = this.props
    const { shouldCloseDialog } = this.state
    return (
      <>
        <TileButton
          onClick={this.shouldCloseDialog}
          style={{
            border: '8px dashed',
            background: 'transparent',
            color: 'white',
            fontWeight: 'normal'
          }}
        >
          +
        </TileButton>
        {
          !shouldCloseDialog
            ? (
              <NewTopSiteInfoModal
                topSitesLength={topSitesLength}
                actions={actions}
                shouldCloseDialog={this.shouldCloseDialog}
              />
            ) : null
        }
      </>
    )
  }
}
