import * as React from 'react'

import { SortableElement } from 'react-sortable-hoc'

// Feature-specific components
import { Tile, TileActionsContainer, TileAction, TileFavicon } from '../../components/default'

// Types
import { NewTab } from '../../__exclude_from_git_common/new_tab_definitions'

// Icons
import { PinIcon, PinOIcon, BookmarkIcon, BookmarkOIcon, CloseStrokeIcon } from 'brave-ui/components/icons'

interface Props {
  actions: any
  value: {
    id: string
    title: string
    url: string
    favicon: string
    background: string
    letter: string
    pinned: boolean
    bookmarked: boolean
  }
}

class TopSite extends React.PureComponent<Props, {}> {
  onTogglePinnedTopSite (site: NewTab.Site) {
    this.props.actions.toggleTopSitePinned(site)
  }

  onIgnoredTopSite (site: NewTab.Site) {
    this.props.actions.removeTopSite(site)
    this.props.actions.showSiteRemovalNotification(true)
  }

  onToggleBookmark (site: NewTab.Site) {
    this.props.actions.toggleTopSiteBookmarked(site.url)
  }

  render () {
    const { value } = this.props
    return (
      <Tile
        title={value.title}
        tabIndex={0}
        style={{
          background: value.background,
          cursor: value.pinned != null ? 'not-allowed' : 'grab'
        }}
      >
        <TileActionsContainer>
          <TileAction title={value.pinned != null ? 'Unpin site' : 'Pin site'} onClick={this.onTogglePinnedTopSite.bind(this, value)}>
            {value.pinned != null ? <PinIcon /> : <PinOIcon />}
          </TileAction>
          <TileAction title={value.bookmarked ? 'Remove site from your bookmarks' : 'Bookmark this site'} onClick={this.onToggleBookmark.bind(this, value)}>
            {value.bookmarked ? <BookmarkIcon /> : <BookmarkOIcon />}
          </TileAction>
          {
            // you can't delete a pinned item
            value.pinned != null
              ? (
                <TileAction title='You can not remove a pinned top site' style={{ opacity: 0.25, cursor: 'not-allowed' }}>
                  <CloseStrokeIcon />
                </TileAction>
              ) : (
                <TileAction title='Remove top site' onClick={this.onIgnoredTopSite.bind(this, value)}>
                  <CloseStrokeIcon />
                </TileAction>
              )
          }
        </TileActionsContainer>
        {
          value.pinned != null
            ? (
              <TileAction
                onClick={this.onTogglePinnedTopSite.bind(this, value)}
                standalone={true}
              >
                <PinIcon />
              </TileAction>
            ) : null
          }
          <a href={value.url}><TileFavicon src={value.favicon} /></a>
      </Tile>
    )
  }
}

export default SortableElement((props: any) => <TopSite {...props} />)
