export const topSitesReducerSetInitialData = (state: any, payload: any) => {
  const emptyGridSites: boolean = state.gridSites.length === 0
  const noGridSitesProperty: boolean = 'gridSites' in state === false

  if (emptyGridSites || noGridSitesProperty) {
    state = topSitesReducerDataUpdated(state, payload.topSites)
  }

  state = { ...state, topSites: payload.topSites }
  return state
}

export const topSitesReducerDataUpdated = (state: any, sitesList: any) => {

  let updatedGridSites: any = []

  // iterate over all items and populate a new array with them.
  // if an item is pinned, add it to its original index inside
  // the new array without removing anything. this will push other
  // items to the right, which is what we want
  for (const site of sitesList) {
    if (site.pinned === undefined) {
      updatedGridSites.push(site)
    } else {
      updatedGridSites.splice(site.pinned, 0, site)
    }
  }

  state = { ...state, gridSites: updatedGridSites }
  return state
}

export const topSitesReducerToggleTopSitePinned = (state: any, payload: any) => {
  const updatedGridSites = state.gridSites.map((site: any, index: any) => {
    if (site.url === payload.pinnedSite.url) {
      return { ...site, pinned: site.pinned ? undefined : index }
    }
    return site
  })
  state = topSitesReducerDataUpdated(state, updatedGridSites)
  return state
}

export const topSitesReducerRemoveSite = (state: any, payload: any) => {
  state = { ...state, ignoredTopSites: [ ...state.ignoredTopSites, payload.ignoredSite ] }

  const filterExcludedFromGridSites = state.gridSites.filter((site: any) => {
    // in the updatedGridSites we only want sites not excluded by the user
    return state.ignoredTopSites.every((excludedSite: any) => excludedSite.url !== site.url)
  })
  state = topSitesReducerDataUpdated(state, filterExcludedFromGridSites)
  return state
}

export const topSitesReducerUndoRemoveSite = (state: any) => {
  if (state.ignoredTopSites.length === 0) {
    // nothing to see here, move along.
    return state
  }
  // remove the last item from the array
  const removedItem = state.ignoredTopSites.pop()
  // push back the item into the grid array by adding the site
  state = topSitesReducerAddSite(state, removedItem)
  return state
}

export const topSitesReducerUndoRemoveAllSites = (state: any) => {
  // erase all ignored sites data
  const allRemovedSites = state.ignoredTopSites
  // remove all sites from the exclusion list
  state = { ...state, ignoredTopSites: [] }
  // add them all back into grid
  state = topSitesReducerAddSite(state, allRemovedSites)
  return state
}

