// IMPORTANT: THIS METHOD SHOULD BE CALLED WHEN TOP SITES ARE ADDED FROM THE API
// AS WELL. TODO CEZARAUGUSTO
export const topSitesReducerAddSite = (state: any, site: any) => {
  let currentGridSitesWithNewItems: any
  if (Array.isArray(site)) {
    currentGridSitesWithNewItems = [ ...site, ...state.gridSites ]
  } else {
    currentGridSitesWithNewItems = [ site, ...state.gridSites ]
  }

  state = topSitesReducerDataUpdated(state, currentGridSitesWithNewItems)
  return state
}

export const topSitesReducerShowSiteRemovalNotification = (state: any, shouldShow: any) => {
  state = { ...state, shouldShowSiteRemovalNotification: shouldShow }
  return state
}
