import { action } from 'typesafe-actions'
import { NewTab } from '../__exclude_from_git_common/new_tab_definitions'
import { InitialData } from '../api/initialData'

// Constants
import { types } from '../constants/top_sites_types'

export const setInitialTopSitesData = (initialData: InitialData) =>
  action(types.TOP_SITES_SET_INITIAL_DATA, initialData)

export const topSitesDataUpdated = (gridSites: NewTab.Site[]) =>
  action(types.TOP_SITES_DATA_UPDATED, { gridSites })

export const toggleTopSitePinned = (pinnedSite: string) =>
  action(types.TOP_SITES_TOGGLE_SITE_PINNED, { pinnedSite })

export const removeTopSite = (ignoredSite: any) =>
  action(types.TOP_SITES_REMOVE_SITE, { ignoredSite })

export const undoRemoveTopSite = () =>
  action(types.TOP_SITES_UNDO_REMOVE_SITE)

export const undoRemoveAllTopSites = () =>
  action(types.TOP_SITES_UNDO_REMOVE_ALL_SITES)

export const toggleTopSiteBookmarked = (url: string) =>
  action(types.TOP_SITES_TOGGLE_SITE_BOOKMARKED, { url })

export const bookmarkInfoAvailable = (queryUrl: string, bookmarkTreeNode: NewTab.Bookmark) =>
  action(types.TOP_SITES_BOOKMARK_INFO_AVAILABLE, { queryUrl, bookmarkTreeNode })

export const addTopSite = (url: string) =>
  action(types.TOP_SITES_ADD_SITE, { url })

export const showSiteRemovalNotification = (shouldShow: boolean) =>
  action(types.TOP_SITES_SHOW_SITE_REMOVAL_NOTIFICATION, { shouldShow })
