import * as React from 'react'

// Feature-specific components
// import { Button, Input } from 'brave-ui'
import {
  getCharForSite,
  generateTopSiteFavicon,
  generateTopSiteId,
  regexpForURL,
  urlPattern
} from '../../helpers/newTabUtils'

interface Props {
  actions: any
  shouldCloseDialog: any
  topSitesLength: number
}

interface State {
  title: string
  url: string
}

export default class NewTopSiteInfoModal extends React.PureComponent<Props, State> {
  constructor (props: Props) {
    super(props)
    this.state = { title: '', url: '' }
  }
  onChangeTitleInput = (event: React.ChangeEvent<HTMLInputElement>) => {
    this.setState({ title: event.currentTarget.value })
  }

  onChangeURLInput = (event: React.ChangeEvent<HTMLInputElement>) => {
    this.setState({ url: event.currentTarget.value })
  }

  onClickConfirm = () => {
    const { title, url } = this.state
    const { topSitesLength } = this.props
    this.props.actions.addTopSite({
      url,
      title,
      letter: getCharForSite({ title, url }),
      id: generateTopSiteId(topSitesLength + 1),
      favicon: generateTopSiteFavicon(url)
    })
  }

  onClose = () => {
    this.props.shouldCloseDialog()
  }

  render () {
    const { title, url } = this.state
    const lazyWrapperStyle: any = {
      position: 'absolute',
      top: 0,
      left: 0,
      width: '100%',
      height: '100%',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      background: 'rgba(255,255,255,0.6)',
      zIndex: 8
    }

    const lazyContentStyle: any = {
      position: 'relative',
      width: '350px',
      background: '#fff',
      borderRadius: '4px',
      padding: '24px'
    }

    const lazyInputStyle: any = {
      display: 'block',
      width: '100%',
      padding: '6px',
      border: '1px solid orange',
      borderRadius: '4px',
      marginBottom: '20px',
      fontSize: '16px'
    }

    const lazyButtonStyle: any = {
      display: 'block',
      WebkitAppearance: 'none',
      width: '100%',
      borderRadius: '4px',
      background: '#000',
      color: '#fff',
      padding: '12px',
      fontSize: '16px',
      marginBottom: '12px',
      cursor: 'pointer'
    }

    return (
      <div style={lazyWrapperStyle}>
        <div style={lazyContentStyle}>
          <h1>Add a top site yourself</h1>
          <form>
          <input
            placeholder='Title'
            style={lazyInputStyle}
            type='text'
            value={title}
            onChange={this.onChangeTitleInput}
          />
          <input
            placeholder='https://yourwebsite.com'
            style={lazyInputStyle}
            type='url'
            pattern={urlPattern.toString()}
            value={url}
            onChange={this.onChangeURLInput}
          />
          <button
            disabled={!regexpForURL(url)}
            style={lazyButtonStyle}
            onClick={this.onClickConfirm}
          >
            Create topsite
          </button>
          <button style={Object.assign({}, lazyButtonStyle, { background: 'orange' })} onClick={this.onClose}>Close this dialog</button>
          </form>
        </div>
      </div>
    )
  }
}
