// API
import getActions from '../api/getActions'
// import * as bookmarksAPI from '../api/bookmarks'
import { getCharForSite, generateTopSiteId, generateTopSiteFavicon } from '../helpers/newTabUtils'

import { NewTab } from '../__exclude_from_git_common/new_tab_definitions'

// Utils
import { debounce } from '../__exclude_from_git_common/debounce'

export const getGridSites = (sites: any /* chrome topsites */, checkBookmarkInfo?: boolean) => {
  let gridSites = sites.slice()
  gridSites.forEach((gridSite: NewTab.Site, index: number) => {
    gridSite.letter = getCharForSite(gridSite)
    gridSite.favicon = generateTopSiteFavicon(gridSite.url)
    // gridSite.bookmarked = state.bookmarks[gridSite.url]
    gridSite.id = generateTopSiteId(index)
  })
  return gridSites
}

/**
 * Calculates the top sites grid and calls an action with the results
 */
export const calculateGridSites = debounce((state: NewTab.PersistentState) => {
  // TODO(petemill):
  // Instead of debouncing at the point of reducing actions to state,
  // and having the reducer call this, it may be more understandable
  // (and performant) to have this be a selector so that the calculation
  // is only performed when the relevant state data is changed.
  getActions().topSitesDataUpdated(state.topSites)
}, 10)
