import * as React from 'react'
// import { DragDropContext } from 'react-dnd'
// import HTML5Backend from 'react-dnd-html5-backend'
import {
  Page,
  Header,
  ClockWidget as Clock,
  Footer,
  App
} from '../../components/default'

// Components
import Stats from './stats'
import TopSitesGrid from './topSitesGrid'
import FooterInfo from './footerInfo'

// Types
import { NewTab } from '../../__exclude_from_git_common/new_tab_definitions'

import SiteRemovalNotification from './siteRemovalNotification'

interface Props {
  actions: any
  newTabData: NewTab.State
  topSitesData: any
}
class NewTabPage extends React.PureComponent<Props, {}> {

  fakeHideWidget = () => {
    return
  }

  render () {
    const { newTabData, topSitesData, actions } = this.props

    if (!newTabData) {
      return null
    }

    return (
      <App dataIsReady={true}>
        <Page>
          <Header>
            <Stats
              stats={newTabData.stats}
            />
            <Clock />
          </Header>
          {
            topSitesData.gridSites.length
            ? <div style={{ margin: '25px 30px 15px' }}>
                <TopSitesGrid
                  actions={actions}
                  gridSites={topSitesData.gridSites}
                  showWidget={true}
                  menuPosition={'right'}
                  hideWidget={this.fakeHideWidget}
                  textDirection={'left'}
                />
              </div>
            : null
          }
          <div style={{ margin: 'auto' }}>
          {
            topSitesData.shouldShowSiteRemovalNotification
            ? (
              <SiteRemovalNotification actions={actions} />
            ) : null
          }
          </div>
          <Footer>
            <FooterInfo />
          </Footer>
        </Page>
      </App>
    )
  }
}

export default NewTabPage
