// React/Redux APIs
import * as React from 'react'
import { render } from 'react-dom'
import { Provider } from 'react-redux'
import LightTheme from 'brave-ui/theme/brave-default'
import DarkTheme from 'brave-ui/theme/brave-dark'
import BraveCoreThemeProvider from './__exclude_from_git_common/braveCoreThemeProvider'
import { wireApiEventsToStore } from './apiEventsToStore'

// Utils
import 'emptykit.css'

// Fonts
import '../fonts/poppins.css'
import '../fonts/muli.css'

// Main component
import App from './containers/app'

// Main store
import store from './store'

function initialize () {
  render(
    (
    <Provider store={store}>
      <BraveCoreThemeProvider
        initialThemeType={'System'}
        dark={DarkTheme}
        light={LightTheme}
      >
        <App />
      </BraveCoreThemeProvider>
    </Provider>
    ),
    document.getElementById('root'))
}

// Get store data going
wireApiEventsToStore()

// Perform DOM-dependent initialization when ready
document.addEventListener('DOMContentLoaded', initialize)
