// React/Redux APIs
import * as React from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'

// Actions
import * as newTabActions from '../actions/new_tab_actions'
import * as topSitesActions from '../actions/top_sites_actions'

// Styles and fonts
import 'emptykit.css'

// Types
import { NewTab } from '../__exclude_from_git_common/new_tab_definitions'

// Components
import NewTabPage from './newTab'

interface Props {
  actions: any
  newTabData: NewTab.State
  topSitesData: any
}

export class App extends React.PureComponent<Props, {}> {
  render () {
    return (
      <div id='featurePage'>
        <NewTabPage
          newTabData={this.props.newTabData}
          topSitesData={this.props.topSitesData}
          actions={this.props.actions}
        />
      </div>
    )
  }
}

const mapStateToProps = (state: any) => {
  return {
    newTabData: state.newTabData,
    topSitesData: state.topSitesData
  }
}

const mapDispatchToProps = (dispatch: any) => {
  const allActions = Object.assign({}, newTabActions, topSitesActions)
  return {
    actions: bindActionCreators(allActions, dispatch)
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App)
